import React, { useContext } from "react";
import { Button, Card, Image, Tooltip, Alert, Badge } from "antd";
import { FileSearchOutlined, SmileFilled } from "@ant-design/icons";
import { getExplorer } from "helpers/networks";
import { Link } from "react-router-dom";
import { NFTCardStyle, NFTsDiv, NFTImg, BtnPrimary, BtnInfo, NFTImgWrapperStyle } from "../GlobalStyles";
import { DemoNFTContracts } from "../MglNftMetadata";
import { AvatarCtx } from "index";
import { useNFTTokenIds } from "hooks/useNFTTokenIds";
import { useVerifyMetadata } from "hooks/useVerifyMetadata";
import { MainChainID } from "../MglNftMetadata";
import { chainIdToNameAndLogo } from "../components/Chains/Chains";
import {
    pageTitleStyle,
    descriptionStyle,
    highlightTextColor
} from "GlobalStyles";
import Loader from "./Loader";

const fallbackImg =
    "data:image/png;base64,";


function DemoAvatar() {
    // eslint-disable-next-line no-unused-vars
    const [avatar, setAvatar] = useContext(AvatarCtx);
    const chainId = MainChainID;
    const chainName = chainIdToNameAndLogo.get(chainId)[0];
    const chainLogo = chainIdToNameAndLogo.get(chainId)[1];
    const demoNFTContract = DemoNFTContracts.get(chainId);
    console.log("demoNFTContract", demoNFTContract);
    const { data: NFTTokenIds, error: NFTsFetchError, isLoading } = useNFTTokenIds(demoNFTContract, 3, chainId);
    console.log("NFTTokenIds", NFTTokenIds);
    const { verifyMetadata } = useVerifyMetadata();

    if (isLoading) {
        return (<Loader />);
    } else {

        return (
            <div style={{

            }}>
                <div style={{
                    marginTop: "1rem",
                }}>
                    <div style={{
                        ...pageTitleStyle,
                    }}>I am a Demo NFT Avatar <SmileFilled style={{ color: "#FFBE59" }} />
                    </div>
                    <div style={{
                        ...descriptionStyle,
                        padding: "1rem 0",
                    }}>
                        <Button
                            style={BtnInfo}
                            onClick={() =>
                                window.open(
                                    `${getExplorer(chainId)}address/${demoNFTContract}`,
                                    "_blank"
                                )
                            }
                        >
                            {demoNFTContract}
                        </Button>
                        <div style={{
                            display: "flex",
                            alignItems: "center",
                            marginBottom: "1rem",
                            marginTop: "0.5rem",
                        }}>
                            ON&nbsp;
                            {chainName}
                            &nbsp;&nbsp;
                            {chainLogo}
                        </div>
                        <p>
                            You can try me first before buying your own NFT Avatar,&nbsp;
                            <span style={{ color: highlightTextColor }}>but I will disappear soon</span>
                            &nbsp;&nbsp;
                        </p>

                        <p>
                            If you dont have your awesome Avatar yet, get one in our&nbsp;&nbsp;
                            <Button
                                type="primary"
                                style={BtnPrimary}
                            >
                                <Link to="/marketplace">
                                    Marketplace{" "}
                                </Link>
                            </Button>
                        </p>
                    </div>
                </div>
                {(
                    <>
                        {NFTsFetchError && (
                            <>
                                <Alert
                                    message="Unable to fetch NFT. We are searching for a solution, please try again later!"
                                    type="warning"
                                />
                                <div style={{ marginBottom: "10px" }}></div>
                            </>
                        )}
                    </>
                )}
                <div style={{
                    ...NFTsDiv,
                    marginTop: "1.5rem",
                }}>
                    {NFTTokenIds?.result
                        .map((nft, index) => {
                            //Verify Metadata
                            nft = verifyMetadata(nft);
                            return (
                                <Card
                                    key={index}
                                    hoverable
                                    actions={[
                                        <Tooltip title="View On Blockexplorer">
                                            <FileSearchOutlined
                                                onClick={() =>
                                                    window.open(
                                                        `${getExplorer(chainId)}address/${nft.token_address}`,
                                                        "_blank"
                                                    )
                                                }
                                            />
                                        </Tooltip>,
                                    ]}
                                    style={NFTCardStyle}
                                    cover={<>
                                        <Image
                                            preview={false}
                                            src={nft.image || "error"}
                                            fallback={fallbackImg}
                                            alt=""
                                            style={NFTImg}
                                            wrapperStyle={{
                                                backgroundColor: "#" + nft?.background_color,
                                                ...NFTImgWrapperStyle
                                            }}
                                        />
                                        <Badge.Ribbon
                                            text="I will disappear soon"
                                            color="red"
                                            style={{
                                                paddingRight: "5px",
                                                paddingLeft: "5px",
                                                marginRight: "1rem",
                                                marginTop: "-1rem",
                                            }}
                                        />
                                    </>
                                    }
                                >
                                    <div
                                        style={{
                                            display: "flex",
                                            justifyContent: "center",
                                            alignItems: "center",
                                            marginTop: "1rem",
                                        }}>
                                        <Button
                                            onClick={() => {
                                                const avatarUri = nft?.image;
                                                const avatarTokenAddress = nft?.token_address;
                                                const avatarTokenId = nft?.token_id;
                                                setAvatar({
                                                    uri: avatarUri,
                                                    tokenAddress: avatarTokenAddress,
                                                    tokenId: avatarTokenId,
                                                });
                                            }}
                                            type="primary"
                                            style={BtnPrimary}
                                        >
                                            <Link to='/play-setup'>
                                                Play with me
                                            </Link>
                                        </Button>
                                    </div>
                                </Card>)
                        })}
                </div>
            </div>
        );
    }
}

export default DemoAvatar;
